export default {
	getMessage_: state => key => {
		if (!state.userMessage.size || !key) return null;

		return state.userMessage && state.userMessage.size > 0 && state.userMessage.get(key) ? state.userMessage.get(key) : key;
	},

	// 버튼 권한 정보를 담고 있는 Map에서 버튼의 라벨 반환
	getButtonMsg_: state => key => {
		if (!key) return null;
		return state.screen.screenBtn && state.screen.screenBtn.get(key) ? state.userMessage.get(state.screen.screenBtn.get(key).msgKey) : key;
	},

	// 권한에 맞는 버튼이 store에 있는지 판별
	isButton_: state => key => {
		return state.screen.screenBtn && state.screen.screenBtn.get(key) ? true : false;
	}
};
