import * as types from './mutation-types';

export default {
	[types.SET_STATE]: (state, payload) => {
		state[payload.key] = payload.value;
	},

	[types.SET_DETAIL_VALUE]: (state, { key, value }) => {
		state.GuideBoardDetailValue[key] = value;
	},

	[types.SET_MODAL_VALUE]: (state, { key, value }) => {
		state.GuideBoardModalValue[key] = value;
	},

	[types.DEFAULT_ASSIGN]: (state, payload) => {
		state[payload.key] = payload.value;
	}
};
