import client from '@/api/client';
import { BASE_URL, MAIN_URL } from '@/constants';

export default {
	getUserMessage: () => {
		return client
			.get(`${MAIN_URL}/cc/v1/messages`)
			.then(response => (response && response.data) || null)
			.catch(error => {
				throw error;
			});
	},

	// 사용자 ID와 화면 ID, 공장 코드를 이용해 사용자의 화면 권한 정보 등을 획득하기 위한 API
	getScreen: ({ plantCd, scrId }) => {
		return client
			.get(`${MAIN_URL}/menu/v1/plants/${plantCd}/screens/${scrId}`)
			.then(response => (response && response.data) || null)
			.catch(error => {
				throw error;
			});
	},

	// 즐겨찾기 등록 API
	setFavorite: ({ plantCd, scrId }) => {
		return client
			.post(`${MAIN_URL}/menu/v1/status/1/plants/${plantCd}/favorites/${scrId}`)
			.then(response => (response && response.data) || null)
			.catch(error => {
				throw error;
			});
	},

	// 즐겨찾기 삭제 API
	deleteFavorite: ({ plantCd, scrId }) => {
		return client
			.post(`${MAIN_URL}/menu/v1/status/3/plants/${plantCd}/favorites/${scrId}`)
			.then(response => (response && response.data) || null)
			.catch(error => {
				throw error;
			});
	}
};
