import * as types from './mutation-types';

export default {
	[types.DEFAULT_ASSIGN]: (state, payload) => {
		state[payload.key] = payload.value;
	},

	[types.SEARCH_PARAM]: (state, payload) => {
		state.searchParams[payload.key] = payload.value;
	},

	[types.SET_SCREEN]: (state, payload) => {
		let screen = { ...state.screen, ...payload.data };

		let screenBtn = new Map();
		screen.btnList.forEach(btn => {
			screenBtn.set(btn.functionType, btn);
		});
		screen.screenBtn = screenBtn;

		screen.pageArray = payload.data.pageSize.map(page => Number(page.commCd));
		screen.defaultPageSize = Number(payload.data.defaultPageSize);

		state.screen = screen;
	},

	[types.SET_USER_MESSAGE]: (state, payload) => {
		const messageMap = new Map();
		payload.data.forEach(element => {
			messageMap.set(element.key, element.value);
		});
		state.userMessage = messageMap;
	}
};
