import { utils } from '@sfaas/util';

export default {
	labels_: state => key => {
		return state.labels && state.labels.size > 0 && state.labels.get(key) ? state.labels.get(key) : key;
	},

	labelValue_: state => key => {},

	category_: state => {
		const { category } = state.searchParams;
		return category;
	},

	dtlCategory_: state => {
		const { dtlCategory } = state.searchParams;
		return dtlCategory;
	},

	selectedCategorys_: state => key => {
		const { dtlCategorys } = state;
		return dtlCategorys.filter(dtlCategory => dtlCategory.type === key);
	},

	kwd_: state => {
		const { kwd } = state.searchParams;
		return kwd;
	},

	currentPage_: state => {
		const { pagingInfo } = state;
		const { currentPage } = pagingInfo;
		return currentPage;
	},

	totalSize_: state => {
		const { pagingInfo } = state;
		const { totalSize } = pagingInfo;
		return totalSize;
	},

	pageUnit_: state => {
		const { pagingInfo } = state;
		const { pageUnit } = pagingInfo;
		return pageUnit;
	},

	historyData_: state => {
		const { historyData } = state;
		return historyData;
	},

	urlSearchParams_: state => {
		const { searchParams, pagingInfo } = state;
		const urlSearchParams = new URLSearchParams();
		Object.keys(searchParams).forEach(key => {
			let value = searchParams[key];
			if (!utils.$sf_isNull(value) && !utils.$sf_isUndefined(value) && !value.includes('ALL')) {
				urlSearchParams.set(key, value);
			}
		});

		Object.keys(pagingInfo).forEach(key => {
			const validKeys = ['currentPage', 'pageUnit'];
			const value = pagingInfo[key];
			if (validKeys.includes(key)) {
				urlSearchParams.set(key, value);
			}
		});
		return urlSearchParams.toString();
	}
};
