import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
import actions from './actions';
import getters from './getters';
import mutations from './mutations';
import state from './state';

export const rootComputed = {
	...mapState('commonStore', Object.keys(state)),
	...mapGetters('commonStore', Object.keys(getters))
};
export const rootMethods = {
	...mapActions('commonStore', Object.keys(actions)),
	...mapMutations('commonStore', Object.keys(mutations))
};
