import api from './api';
import * as types from './mutation-types';

export default {
	setUser_({ commit }, value) {
		commit(types.DEFAULT_ASSIGN, {
			key: 'user',
			value
		});
	},

	setScreen_({ commit }, value) {
		commit(types.DEFAULT_ASSIGN, {
			key: 'screen',
			value
		});
	},

	getUserMessage_({ state, commit }) {
		// data: [
		// 	{
		// 		key: 'type1',
		// 		value: '과자'
		// 	},
		// 	{
		// 		key: 'type2',
		// 		value: '음료수'
		// 	},
		// 	{
		// 		key: 'type3',
		// 		value: '차'
		// 	}
		// ];

		// commit(types.SET_USER_MESSAGE, {
		// 	data: data
		// });

		// return data;

		return api
			.getUserMessage()
			.then(data => {
				if (data.header.code === 0) {
					commit(types.SET_USER_MESSAGE, {
						data: data.body
					});
				}

				return data.header;
			})
			.catch(error => {
				throw error;
			});
	},

	getScreen_({ state, commit }) {
		return api
			.getScreen({ scrId: state.screen.scrId, plantCd: state.user.plantCd })
			.then(data => {
				if (data.header.code === 0) {
					commit(types.SET_SCREEN, {
						data: data.body
					});
				}

				return data.header;
			})
			.catch(error => {
				throw error;
			});
	},

	onFavorite_({ state, commit }) {
		if (state.screen.favoriteYn === 'Y') {
			return api
				.deleteFavorite({ scrId: state.screen.scrId, plantCd: state.user.plantCd })
				.then(data => {
					if (data.header.code === 0) {
						commit(types.DEFAULT_ASSIGN, {
							key: 'screen',
							value: { ...state.screen, favoriteYn: 'N' }
						});
					}

					return data.header;
				})
				.catch(error => {
					throw error;
				});
		} else {
			return api
				.setFavorite({ scrId: state.screen.scrId, plantCd: state.user.plantCd })
				.then(data => {
					if (data.header.code === 0) {
						commit(types.DEFAULT_ASSIGN, {
							key: 'screen',
							value: { ...state.screen, favoriteYn: 'Y' }
						});
					}

					return data.header;
				})
				.catch(error => {
					throw error;
				});
		}
	}
};
