const DefaultLayout = () => import('@common/Layout/DefaultLayout');
const NoPermission = () => import('@common/Page/NoPermission');
import views from '@common';
const children = Object.keys(views).map(scrName => ({ path: `/${scrName}`, component: views[scrName] }));
const routes = [
	{
		path: '/NoPermission',
		component: NoPermission
	},
	{
		path: '/',
		component: DefaultLayout,
		children
	}
];

export default routes;
